body {
  font-family: Monospace;
  margin: 0;
  padding: 0;

}

.app{
  font-family: Monospace;
}

.header {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
 
}

.logo {
  width: 100px;
}

.nav-items > ul {
  display: flex;
  list-style-type: none;
  font-size: 24px;
  
}

.nav-items > ul > li {
    padding: 0px 100px;
    margin: 10px; 
}

.res-container{
  display: flex;
  flex-wrap: wrap;
}

.res-card {
    width: 200px;
    height: 300px;
    margin: 10px;
    border: none;
    padding: 5px 5px;
    cursor: pointer;
    background-color:rgb(232, 232, 232);
}

.res-container :hover{
  border: 1px solid black;
}

.Meghana-img{
  width: 100%;
  height: 60%;
}

.search{
    padding: 10px;
    margin: 10px 0px;
}

